import React from "react";
import { Col, Image, Row } from "react-bootstrap";
import {
  ICGithubSquareSvg,
  ICFacebookSvg,
  ICInstagramSvg,
  ICTwitterSvg,
  imgDerian,
  ICLinkedInSvg,
} from "../../assets";
import "./Welcome.scss";

const Welcome = () => {
  return (
    <Row className="welcome-page">
      <Col>
        <Row>
          <Col
            md={4}
            className="d-flex justify-content-center justify-content-lg-start"
          >
            <img src={imgDerian} alt="avatar" />
          </Col>
          <Col className="d-flex flex-column justify-content-center pl-md-5 pl-lg-4 pl-xl-0 align-items-center align-items-md-start text-center text-md-left py-4">
            <h4>Hi, I am</h4>
            <h1>Derian Indra Pratama</h1>
            <h3>Web Developer</h3>
            <a
              href="/CV DERIAN INDRA PRATAMA V6.pdf"
              alt="cv"
              className="btn btn-primary mt-3"
              target="_blank"
            >
              Download CV
            </a>
          </Col>
        </Row>
        <hr />
        <Row>
          <Col>
            <p>
              I am a person who has passion in Information Technology,
              especially in programming. I have an enthusiasm with something
              that concerned with the Web Programming.
            </p>
          </Col>
        </Row>
        <Row className="mt-3">
          <Col md={{ span: 6, offset: 3 }}>
            <Row>
              <Col className="d-flex justify-content-center">
                <a href="https:facebook.com/derian.7" target="blank_">
                  <Image src={ICFacebookSvg} className="icon" />
                </a>
              </Col>
              <Col className="d-flex justify-content-center">
                <a href="https:twitter.com/derianip" target="blank_">
                  <Image src={ICTwitterSvg} className="icon" />
                </a>
              </Col>
              <Col className="d-flex justify-content-center">
                <a href="https:instagram.com/derian.pratama" target="blank_">
                  <Image src={ICInstagramSvg} className="icon" />
                </a>
              </Col>
              <Col className="d-flex justify-content-center">
                <a href="https:linkedin.com/in/derianip" target="blank_">
                  <Image src={ICLinkedInSvg} className="icon" />
                </a>
              </Col>
              <Col className="d-flex justify-content-center">
                <a href="https:facebook.com/derian.7" target="blank_">
                  <Image src={ICGithubSquareSvg} className="icon" />
                </a>
              </Col>
            </Row>
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default Welcome;
