const portPolioState = {
  data: [],
  modalShow: [],
};

const portfolioReducer = (state = portPolioState, action) => {
  if (action.type === "SET_PORTFOLIO") {
    return {
      ...state,
      data: action.value,
    };
  }

  if (action.type === "SET_MODAL_PORTFILIO") {
    return {
      ...state,
      modalShow: action.value,
    };
  }

  return state;
};

export default portfolioReducer;
