import React from "react";

const TimelineList = ({ date, title, body }) => {
  return (
    <div className="entry">
      <div className="title">
        <h3>{date}</h3>
      </div>
      <div className="body">
        <p className="t-title">{title}</p>
        <p className="t-body">{body}</p>
      </div>
    </div>
  );
};

export default TimelineList;
