const WorkExperience = () => {
  return (
    <Row>
      <Col>
        <HeaderContent title="Work Experience" />
        <Row>
          <Col md={10}>
            <Timeline
              data={[
                {
                  id: 1,
                  date: "2021 - Present",
                  title:
                    "System Analys / PT. Electronic Data Interchange Indonesia",
                  body:
                    "PT. Electronic Data Interchange Indonesia (EDII), which is a subsidiary of PT Pelabuhan Indonesia II (Persero) is engaged in digital solutions, Current users of EDII services include government agencies, importers, exporters, shipping companies, banks, pharmaceutical.",
                },
                {
                  id: 2,
                  date: "2017 - 2021",
                  title:
                    "Web Developer / PT. Electronic Data Interchange Indonesia",
                  body:
                    "PT. Electronic Data Interchange Indonesia (EDII), which is a subsidiary of PT Pelabuhan Indonesia II (Persero) is engaged in digital solutions, Current users of EDII services include government agencies, importers, exporters, shipping companies, banks, pharmaceutical.",
                },
              ]}
            />
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default WorkExperience;
