import React from "react";
import { Col, Row } from "react-bootstrap";
import { HeaderContent, Timeline } from "../../components";

const Education = () => {
  return (
    <Row>
      <Col>
        <HeaderContent title="My Education" />
        <Row>
          <Col md={10}>
            <Timeline
              data={[
                {
                  id: 1,
                  date: "2011 - 2017",
                  title: "Bachelor Degree / Indonesia Computer University",
                  body:
                    "Bachelor Degree of Infomatics Engineering from Indonesia Computer University. Start learning programming seriously and started many programming projects at the college.",
                },
                {
                  id: 2,
                  date: "2008 - 2011",
                  title: "Senior Hight School / SMK 2 Cianjur",
                  body:
                    "Software Engineering major, I learned programming here for the first time. Start learn a programming language with HTML, PHP, Pascal & MySql.",
                },
              ]}
            />
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default Education;
