import {
  faBriefcase,
  faFileInvoice,
  faHome,
  faIdCard,
  faIdCardAlt,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";
import { Col, Nav, Navbar, Row } from "react-bootstrap";
import { Link, withRouter } from "react-router-dom";
import "./MenuBar.scss";

const MenuBar = (props) => {
  const path = props.location.pathname;
  const pathname = path === "/" ? "welcome" : path.replace("/", "");
  const [selected, setSelected] = useState({ [pathname]: "selected" });

  const handleSelect = (menu) => {
    setSelected({ [menu]: "selected" });
  };
  return (
    <Row className="menu-bar-wrapper">
      <Col>
        <Navbar expanded="lg" className="p-0 m-0">
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="m-auto nav-link">
              <Link
                to="/welcome"
                className={`mr-4 menu-item text-center ${selected.welcome}`}
                onClick={() => handleSelect("welcome")}
              >
                <FontAwesomeIcon
                  icon={faHome}
                  size="lg"
                  className="d-md-none d-block"
                />
                <span className="d-none d-md-block">Welcome</span>
              </Link>
              <Link
                to="/profile"
                className={`mr-4 menu-item text-center ${selected.profile}`}
                onClick={() => handleSelect("profile")}
              >
                <FontAwesomeIcon
                  icon={faIdCardAlt}
                  size="lg"
                  className="d-md-none d-block"
                />
                <span className="d-none d-md-block">Profile</span>
              </Link>
              <Link
                to="/resume"
                className={`mr-4 menu-item text-center ${selected.resume}`}
                onClick={() => handleSelect("resume")}
              >
                <FontAwesomeIcon
                  icon={faFileInvoice}
                  size="lg"
                  className="d-md-none d-block"
                />
                <span className="d-none d-md-block">Resume</span>
              </Link>
              <Link
                to="/portfolio"
                className={`mr-4 menu-item text-center ${selected.portfolio}`}
                onClick={() => handleSelect("portfolio")}
              >
                <FontAwesomeIcon
                  icon={faBriefcase}
                  size="lg"
                  className="d-md-none d-block"
                />
                <span className="d-none d-md-block">Portfolio</span>
              </Link>
              <Link
                to="/contact"
                className={`menu-item text-center ${selected.contact}`}
                onClick={() => handleSelect("contact")}
              >
                <FontAwesomeIcon
                  icon={faIdCard}
                  size="lg"
                  className="d-md-none d-block"
                />
                <span className="d-none d-md-block">Contact</span>
              </Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <hr />
      </Col>
    </Row>
  );
};

export default withRouter(MenuBar);
