import React from "react";
import { Col, Image, Row } from "react-bootstrap";
import {
  ICHtmlSvg,
  ICJavaScriptSvg,
  ICReactSvg,
  ICSenchaSvg,
  ICCssSvg,
  ICBootstrapSvg,
  ICPhpSvg,
  ICXDSvg,
  ICFigmaSvg,
  ICOraSqlSvg,
  ICSass,
} from "../../assets";
import { HeaderContent } from "../../components";
import "./Skil.scss";

const Skill = () => {
  return (
    <Row>
      <Col md={{ span: 10, offset: 1 }}>
        <HeaderContent title="My Skill" />
        <Row className="d-flex justify-content-center align-items-center">
          <Col md={2} xs={2} className="text-center mb-5">
            <Image src={ICHtmlSvg} width="50" className="img-fluid" />
          </Col>
          <Col md={2} xs={2} className="text-center mb-5">
            <Image src={ICCssSvg} width="50" className="img-fluid" />
          </Col>
          <Col md={2} xs={2} className="text-center mb-5">
            <Image src={ICSass} width="50" className="img-fluid" />
          </Col>
          <Col md={2} xs={2} className="text-center mb-5">
            <Image src={ICJavaScriptSvg} width="50" className="img-fluid" />
          </Col>
          <Col md={2} xs={2} className="text-center mb-5">
            <Image src={ICReactSvg} width="50" className="img-fluid" />
          </Col>
          <Col md={2} xs={2} className="text-center mb-5">
            <Image src={ICSenchaSvg} width="30" className="img-fluid" />
          </Col>
          <Col md={2} xs={2} className="text-center mb-5">
            <Image src={ICBootstrapSvg} width="50" className="img-fluid" />
          </Col>
          <Col md={2} xs={2} className="text-center mb-5">
            <Image src={ICPhpSvg} width="50" className="img-fluid" />
          </Col>
          <Col md={2} xs={2} className="text-center mb-5">
            <Image src={ICXDSvg} width="50" className="img-fluid" />
          </Col>
          <Col md={2} xs={2} className="text-center mb-5">
            <Image src={ICFigmaSvg} width="30" className="img-fluid" />
          </Col>
          <Col md={2} xs={2} className="text-center mb-5">
            <Image src={ICOraSqlSvg} width="50" className="img-fluid" />
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default Skill;
