import {
  faAt,
  faCalendarAlt,
  faGlobeAsia,
  faMapMarkerAlt,
  faMobileAlt,
  faUser,
} from "@fortawesome/free-solid-svg-icons";
import React from "react";
import { Col, Row } from "react-bootstrap";
import { HeaderContent, ListItem } from "../../components";
import "./AboutMe.scss";

const AboutMe = () => {
  return (
    <Row>
      <Col>
        <HeaderContent title="About Me" />
        <Row>
          <Col md={{ span: 10, offset: 1 }}>
            <ListItem label="Name" value="Derian Indra Pratama" icon={faUser} />
            <ListItem
              label="Date of birth"
              value="07 April 1993"
              icon={faCalendarAlt}
            />
            <ListItem
              label="Phone"
              value="+62 858 6172 8801"
              icon={faMobileAlt}
            />
            <ListItem
              label="Email"
              value="derian.indra@outlook.com"
              icon={faAt}
            />
            <ListItem
              label="Web"
              value="derianip.github.io"
              icon={faGlobeAsia}
            />
            <ListItem
              label="Address"
              value="Jakarta, Indonesia"
              icon={faMapMarkerAlt}
            />
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default AboutMe;
