import React from "react";
import { TimelineList } from "..";
import "./Timeline.scss";

const Timeline = ({ data }) => {
  return (
    <div className="timeline">
      {data.map((item) => {
        return (
          <TimelineList
            key={item.id}
            date={item.date}
            title={item.title}
            body={item.body}
          />
        );
      })}
    </div>
  );
};

export default Timeline;
