import React, { useEffect, useState } from "react";
import { Col, Image, Row, Tab, Tabs } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  ICBootstrap,
  ICCss,
  ICCssSvg,
  ICExpress,
  ICExtjs,
  ICFigmaSvg,
  ICFirebaseSvg,
  ICGithubSvg,
  ICGitSvg,
  ICHtml,
  ICHtmlSvg,
  ICLaravel,
  ICOraSqlSvg,
  ICPhp,
  ICPhpSvg,
  ICReactjs,
  ICReactNative,
  ICRedux,
  ICSass,
  ICSassSvg,
  ICSenchaSvg,
  ICVSCodeSvg,
  ICXDSvg,
  imgBilling,
  imgFoodMarket,
  imgMyDoctor,
  imgMystore,
  imgNpks,
  imgResume,
} from "../../assets";
import { HeaderContent, ModalRJ } from "../../components";
import { setDataPortfolio } from "../../redux/action";
import "./Portfolio.scss";

const Portfolio = () => {
  const [show, setShow] = useState([]);
  const { data } = useSelector((state) => state.portfolioReducer);
  const dispacth = useDispatch();

  useEffect(() => {
    const arrData = [
      {
        id: 1,
        title: "NPK-S",
        desc:
          "An operating system that handles container activities on line 2, including stuffing and stripping activities, monitoring yard & container movement which is integrated with the new billing system (NBS) payment process.",
        role: "FullStack",
        techno: [
          ICHtmlSvg,
          ICCssSvg,
          ICSassSvg,
          ICPhpSvg,
          ICExpress,
          ICSenchaSvg,
          ICOraSqlSvg,
        ],
        tools: [ICGithubSvg, ICGitSvg, ICVSCodeSvg],
        image: imgNpks,
        category: "web",
      },
      {
        id: 2,
        title: "NPK-S Billing",
        desc:
          "Payment system for non-container stuffing stripping (NPKS) activities including receiving delivery container & container movement costs,this system is integrated with the NPKS Terminal operating system.",
        role: "Front End Developer",
        techno: [ICHtml, ICCss, ICSass, ICPhp, ICExpress, ICExtjs, ICOraSqlSvg],
        tools: [ICGithubSvg, ICGitSvg, ICVSCodeSvg],
        image: imgBilling,
        category: "web",
      },
      {
        id: 3,
        title: "Resume",
        desc: "derianip.github.io",
        techno: [ICHtml, ICCss, ICBootstrap, ICSass, ICReactjs],
        tools: [ICFigmaSvg, ICGithubSvg, ICGitSvg, ICVSCodeSvg],
        image: imgResume,
        role: "-",
        category: "web",
      },
      {
        id: 4,
        title: "My Store",
        desc: "derianip.github.io/my-store",
        techno: [ICHtml, ICCss, ICBootstrap, ICSass, ICReactjs],
        tools: [ICXDSvg, ICGithubSvg, ICGitSvg, ICVSCodeSvg],
        image: imgMystore,
        role: "-",
        category: "web",
      },
      {
        id: 5,
        title: "Hello Doctor",
        desc: "derianip.github.io",
        techno: [ICReactNative, ICRedux, ICFirebaseSvg],
        tools: [ICFigmaSvg, ICGithubSvg, ICGitSvg, ICVSCodeSvg],
        image: imgMyDoctor,
        role: "Full Stack Developer",
        category: "mobile",
      },
      {
        id: 6,
        title: "Food Market",
        desc: "derianip.github.io",
        techno: [ICReactNative, ICRedux, ICLaravel],
        tools: [ICFigmaSvg, ICGithubSvg, ICGitSvg, ICVSCodeSvg],
        image: imgFoodMarket,
        role: "Full Stack Developer",
        category: "mobile",
      },
    ];
    dispacth(setDataPortfolio(arrData));
  }, [dispacth]);

  const modalHandler = (i, isShow) => {
    setShow({
      [i]: isShow,
    });
  };

  return (
    <Row className="portfolio-wrapper">
      <Col md={{ span: 10, offset: 1 }}>
        <HeaderContent title="My Portfolio" />
        <Row>
          <Col className="text-center mb-3">
            <Tabs
              variant="pills"
              className="d-flex justify-content-center pt-0"
            >
              <Tab
                eventKey="All"
                title="All"
                tabClassName="mb-5 tab-text d-flex align-items-start align-items-start"
              >
                <Row className="content d-flex justify-content-start align-items-start">
                  {data.map((item) => {
                    return (
                      <Col xs={6} md={4} className="mb-3 coll" key={item.id}>
                        <Image
                          src={item.image}
                          alt={item.title}
                          fluid
                          className="thumb"
                        />
                        <span onClick={() => modalHandler(item.id, true)} />
                        <ModalRJ
                          show={show[item.id]}
                          handleClose={() => modalHandler(item.id, false)}
                          title={item.title}
                          desc={item.desc}
                          role={item.role}
                          tools={item.tools}
                          image={item.image}
                          techno={item.techno}
                        />
                      </Col>
                    );
                  })}
                </Row>
              </Tab>
              <Tab eventKey="web" title="Web" tabClassName="mb-5 tab-text">
                <Row className="content d-flex justify-content-start align-items-start">
                  {data.map((item) => {
                    if (item.category === "web") {
                      const modalId = `${item.category}_${item.id}`;
                      return (
                        <Col xs={6} md={4} className="mb-3 coll" key={item.id}>
                          <Image
                            src={item.image}
                            alt={item.title}
                            fluid
                            className="thumb"
                          />
                          <span onClick={() => modalHandler(modalId, true)} />
                          <ModalRJ
                            show={show[modalId]}
                            handleClose={() => modalHandler(modalId, false)}
                            title={item.title}
                            desc={item.desc}
                            role={item.role}
                            tools={item.tools}
                            image={item.image}
                            techno={item.techno}
                          />
                        </Col>
                      );
                    }
                    return false;
                  })}
                </Row>
              </Tab>
              <Tab
                eventKey="mobile"
                title="Mobile"
                tabClassName="mb-5 tab-text"
              >
                <Row className="content d-flex justify-content-start align-items-start">
                  {data.map((item) => {
                    if (item.category === "mobile") {
                      const modalId = `${item.category}_${item.id}`;
                      return (
                        <Col xs={6} md={4} className="mb-3 coll" key={item.id}>
                          <Image
                            src={item.image}
                            alt={item.title}
                            fluid
                            className="thumb"
                          />
                          <span onClick={() => modalHandler(modalId, true)} />
                          <ModalRJ
                            show={show[modalId]}
                            handleClose={() => modalHandler(modalId, false)}
                            title={item.title}
                            desc={item.desc}
                            role={item.role}
                            tools={item.tools}
                            image={item.image}
                            techno={item.techno}
                          />
                        </Col>
                      );
                    }
                    return false;
                  })}
                </Row>
              </Tab>
            </Tabs>
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default Portfolio;
