import React from "react";
import { Button, Col, Image, Modal, Row } from "react-bootstrap";
import {
  ICBitbucket,
  ICCi,
  ICCss,
  ICExpress,
  ICExtjs,
  ICFigma,
  ICFirebase,
  ICGit,
  ICGithub,
  ICHtml,
  ICLaravel,
  ICMongo,
  ICPhp,
  ICReactjs,
  ICSass,
  ICSql,
  ICXd,
} from "../../../assets";

const ModalRJ = (props) => {
  const { show, handleClose, title, techno, role, desc, image, tools } = props;
  return (
    <Modal
      show={show}
      size="lg"
      dialogClassName="modal-90w"
      onHide={handleClose}
    >
      <Modal.Body>
        <Row>
          <Col className="text-center">
            <h5>
              <strong>{title}</strong>
            </h5>
            <hr />
            <Row>
              <Col md={{ span: 10, offset: 1 }}>
                <Image src={image} alt="npks" className="img-fluid" />
              </Col>
            </Row>
            <Row className="mt-2">
              <Col style={{ fontSize: 13, fontWeight: 300 }}>
                <p>{desc}</p>
              </Col>
            </Row>
            <hr />
            <h5>
              <strong>Technology</strong>
            </h5>
            <Row className="mt-5 d-flex justify-content-center">
              {techno.map((item, i) => {
                return (
                  <Col xs={4} md className="mb-3" key={i}>
                    <Image
                      key={i}
                      src={item}
                      alt="html"
                      width={32}
                      height={32}
                      className="img-fluid"
                    />
                  </Col>
                );
              })}
            </Row>
            <hr />
            <h5>
              <strong>Role</strong>
            </h5>
            <p>{role}</p>
            <hr />
            <h5>
              <strong>Tools</strong>
            </h5>
            <Row className="mt-5 d-flex justify-content-center">
              {tools.map((item, i) => {
                return (
                  <Col xs={4} md className="mb-3" key={i}>
                    <Image
                      key={i}
                      src={item}
                      alt="html"
                      width={32}
                      height={32}
                      className="img-fluid"
                    />
                  </Col>
                );
              })}
            </Row>
            <hr />
            <Button onClick={handleClose}>Close</Button>
            <hr />
            <p style={{ fontSize: 13 }}>I'm, familiar with</p>
            <Row>
              <Col>
                <Image src={ICHtml} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICCss} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICPhp} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICGithub} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICSass} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICExtjs} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICExpress} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICFigma} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICXd} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICBitbucket} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICGit} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICMongo} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICFirebase} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICSql} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICReactjs} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICLaravel} alt="html" width={16} height={16} />
              </Col>
              <Col>
                <Image src={ICCi} alt="html" width={16} height={16} />
              </Col>
            </Row>
          </Col>
        </Row>
      </Modal.Body>
    </Modal>
  );
};

export default ModalRJ;
